import React, { useState, useContext, useEffect, useRef, useMemo } from "react";
import "./Task4.css";
import { Context } from "../../Context";
import { HeadingText } from "../HeadingText/HeadingText";

import leftArrow from "./img/leftArrow.svg";
import rightArrow from "./img/rightArrow.svg";

import giraffe from "./img/DO_3-4_21_1_9.jpg";
import elephant from "./img/DO_3-4_21_1_10.jpg";
import rhino from "./img/DO_3-4_21_1_11.jpg";
import tiger from "./img/DO_3-4_21_1_12.jpg";
import lion from "./img/DO_3-4_21_1_13.jpg";
import zebra from "./img/DO_3-4_21_1_14.jpg";
import monkey from "./img/DO_3-4_21_1_15.jpg";

// Контент модального окна - Алфавит
const alphabetModalContent = () => {
    return (
        <>
            <h3> Словать</h3>
            <br />
            <p>Много разных слов</p>
        </>
    );
};

// Контент модального окна - Помощь
const helpModalContent = () => {
    return (
        <>
            <h3>Электронный помощник</h3>
            <br />
            <p>Контент направленный на помощь в обучении</p>
        </>
    );
};

// Изменение заголовка (поддерживаеи html тэги)
// START
// Верхний заголовок - название урока ( <> текст размещать внутри тэгов </> )
const lessonHeading = () => (
    <>
        <p>Карточки</p>
    </>
);
// Заголовок с заданием к уроку ( <> текст размещать внутри тэгов </> )
const themeHeading = () => (
    <>
        <p style={{ textAlign: 'center' }}>Дикие животные.
        </p>
    </>
);
// Текс title с переводом (НЕ поддерживает html тэги)
const titleHeading = "Диалоговый симулятор";

const ButtonLeft = ({ onClick }) => (
    <div className="sliderContent_button leftButton" onClick={onClick}><img src={leftArrow} style={{ margin: "15px 0 0 18px" }} alt="" /></div>
);
const ButtonRight = ({ onClick }) => (
    <div className="sliderContent_button rightButton" onClick={onClick}><img src={rightArrow} style={{ margin: "15px 0 0 20px" }} alt="" /></div>
);
const CounterTextActual = ({ sliderNumber, actualSlide }) => (
    <div className="sliderContent_sliderCounter">
        <span className="sliderContent_sliderCounter__actual">{actualSlide}</span>/{sliderNumber}
    </div>
);


function Task4() {
    const {
        helpSetIcon,
        alphabetSetIcon,
        helpSetModalContent,
        alphabetSetModalContent,
        lessonSetStartAction,
        lessonSetResetTask,
        resetTask,
        lessonSetCheckTask,
        checkTask,
        lessonSetAndAnswer
    } = useContext(Context);
    // Иконки правого поля  - (помощь и алфавит)
    useEffect(() => {
        //  helpSetIcon - иконка электронного помощника (включить - true, отключить - false)
        helpSetIcon(true);
        //  alphabetSetIcon - иконка алфавита (включить - true, отключить - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);


    const sliderContent = [
        {
            id: 'giraffe',
            src: giraffe
        },
        {
            id: 'elephant',
            src: elephant
        },
        {
            id: 'rhino',
            src: rhino
        },
        {
            id: 'tiger',
            src: tiger
        },
        {
            id: 'lion',
            src: lion
        },
        {
            id: 'zebra',
            src: zebra
        },
        {
            id: 'monkey',
            src: monkey
        },
    ];

    const sliderContentNumber = sliderContent.length;
    const [actualSlide, setActualSlide] = useState(0);



    const actualStartTextNow = actualSlide - 1;

    const rightClickHandler = () => {
        const nextSlide = actualSlide + 1;

        if (nextSlide > (sliderContentNumber - 1)) return;
        setActualSlide(nextSlide)
    }

    const leftClickHandler = () => {
        const nextSlide = actualSlide - 1;
        console.log()
        console.log(actualSlide)

        if (actualSlide === 0) return;
        setActualSlide(nextSlide)
    }

    //  Внесение изменений в контент задания
    return (
        <div className="lessonContent">
            <div className="lessonContent_slider">
                <HeadingText
                    lessonHeading={lessonHeading()}
                    themeHeading={themeHeading()}
                    titleHeading={titleHeading}
                    // soundSource={soundSource}
                    soundVisible={false} titleVisible={false}
                />

                <div className="sliderContent">
                    <ButtonLeft onClick={leftClickHandler} />
                    <div className="sliderContent_slide">
                        <img className="slide" src={sliderContent[actualSlide].src} />
                    </div>
                    <ButtonRight onClick={rightClickHandler} />
                </div>
                <CounterTextActual sliderNumber={sliderContentNumber} actualSlide={actualSlide + 1} />
            </div>
        </div>
    );
}
export default Task4;
